<template>
    <svg preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 -30 1920 948.52">
      <defs />
      <title>bg_wave</title>
      <g id="Ebene_2" data-name="Ebene 2">
        <g id="Ebene_1-2" data-name="Ebene 1">
          <path
            class="cls-1"
            :d="`M0,776.24.76,${46.71 + 10 * move2}s297.05-${40.3 - 3 * move},499,${14.16 - move1 * 5}c175.14,${39.42 + move2 * 5},610.77-53.41,815-${60.55 - move2*10}C1467.57-5,1920,${58.67 - move2 * 20},1920,${58.67 - move * 10}V776.24s-146.89,${56.67 + 3* move1}-${465.7 + move2 * 20},${124.21 + move1 * 10}C1203,953.69,${740 + 5* move2},726.14,${402.23 - move2 * 100},${729.56 + move * 2},${167.12 + 10 * move},732,0,776.24,0,776.24Z`"
          />
        </g>
      </g>
    </svg>
</template>

<script>
export default {

    data: function () {
        return {
            frame: 0,
            move: 0,
            move1: 0,
            move2: 0,
        };
    },
    methods: {
        update(){
            this.frame += 1;
            this.frame = this.frame % 90;
            this.move = Math.sin(this.frame * 4 * Math.PI / 90) 
            this.move1 = Math.sin(this.frame * 3 * Math.PI / 90) 
            this.move2 = Math.sin(this.frame * 2 * Math.PI / 90) 
        }
    },
    mounted(){
        setInterval(this.update, 50);
    }
}


</script>

